.App {
  text-align: center;
  margin-top: 50px;
}

h1 {
  font-size: 3em;
}

#canvas-div {
  margin: 50px 0;
}

#buttons {
  background-color: gray;
  width: 50%;
  padding: 50px 0;
  margin: 25px auto;
}

button, input.upload {
  background-color: lightblue;
  font-size: 2em;
  border: none;
  outline: none;
}

#buttons * {
  font-size: 2em;
}

#upload {
  width: 450px;
  margin-right: 25px;
}

#download-button {
  position: fixed;
  left: 5%;
  top: 5%;
}

.hidden {
  display: none;
}

#download-screen {
  position: fixed;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.75);
  z-index: 2;
}

#download-link-div {
  position: relative;
  height: 300px;
  width: 500px;
  background-color: white;
  border-radius: 10px;
  margin: 30% auto;
  text-align: center;
}

#download-link-div a {
  line-height: 3.5em;
  font-size: 5em;
}

#x-box {
  position: absolute;
  height: 15px;
  width: 15px;
  left: 5%;
  top: 5%;
  color: white;
  background-color: red;
  border: 1px solid red;
  padding: 15px;
  border-radius: 100%;
}


